module New.State

open Types
open Common
open Elmish
open Fable.Core.JsInterop

let editTree tree =
    { nameField = tree.name
      profileNft = tree.profileNft
      bioField = tree.bio
      hasChanges = false
      linkFields =
        tree.links |> Array.mapi (fun i link ->
            { name = link.name
              url = link.url
              nameField = link.name
              urlField = link.url
              editing = false
              index = i
              key = System.Guid.NewGuid().ToString() })
      nftFields = tree.nfts
      colors = tree.colors
      darkMode = tree.darkMode }

let update msg page common model : Model * Cmd<Msg> =         
    match msg with
    | SetAddress address ->
        { model with address = Some address },
            Cmd.batch
                [Cmd.OfAsync.perform Server.getNFTs address SetNfts
                 Cmd.OfAsync.perform Server.getTreeData address SetTreeData]
    | SetEns ens ->
        { model with ens = ens },
            Cmd.batch
                [Cmd.OfAsync.perform Server.getNFTs model.address.Value SetNfts
                 Cmd.OfAsync.perform Server.getTreeData model.address.Value SetTreeData]
    | SetNfts nfts ->
        Browser.Dom.window?nfts <- nfts
        let maxIndex =
            match nfts.results with
            | [||] -> 0
            | _ -> nfts.results |> Array.collect (fun n -> n.nfts |> Array.map (fun c -> c.index)) |> Array.max
        { model with nfts = Some nfts; maxNFTindex = maxIndex }, Cmd.none
    | SetTreeData tree ->
        let cmd =
            match tree.profileNft with
            | None -> Cmd.none
            | Some (tokenAddress, tokenId) -> Cmd.OfAsync.perform (Server.getNFT tokenAddress) tokenId SetProfileNft
        match model.mode with
        | View ->
            { model with tree = Some tree }, cmd
        | Edit ->
            { model with tree = Some tree; treeEdit = Some <| editTree tree }, cmd
    | SetProfileNft nft ->
        { model with profileNft = nft }, Cmd.none
    | TransactionSuccess (txType, receipt) ->
        // upload receipt to server
        Cmd.OfAsync.start (Server.postReceipt receipt)
        let model = 
            match txType with
            | Save ->
                    // AFTER SAVE TREE
                match model.treeEdit with
                | Some tree ->
                    let tree = { tree with hasChanges = false }
                    { model with treeEdit = Some tree; isSaving = None }
            | PostLike ->
                    // AFTER POST LIKE
                match model.nftDetails, model.nfts, common.accountData with
                | Some nft, Some nfts, Some { profileInfo = Some (Some profileInfo); selectedAccount = address } ->
                    let like =
                        { userAddress = address
                          userDisplay = profileInfo.displayName
                          userImage = profileInfo.image
                          userUrl = ""
                          text = System.String.Join(" ", model.likeEmojiInput)
                          timestamp = 0L
                          blockNumber = receipt?blockNumber }
                    let likes = Array.append [| like |] nft.likes
                    let nftResults =
                        nfts.results |> Array.map (fun c ->
                            if c.collection.token_address = nft.collection.token_address then
                                let nfts = c.nfts |> Array.map (fun n ->
                                    if n.nft.token_id = nft.nft.token_id then
                                        let likes =
                                            match n.likes with          // TODO - handle more than 2 likes
                                            | null | [||] -> [|{ text = like.text }|]
                                            | [| a |] | [| a; _ |] -> [|{ text = like.text }; a|]
                                        { n with likeCount = n.likeCount + 1; likes = likes }
                                    else n)
                                { c with nfts = nfts }
                            else c)
                    { model with isSaving = None; likeEmojiInput = []; nftDetails = Some { nft with likes = likes; displayReactionBoard = false  }; nfts = Some { nfts with results = nftResults } }
            | _ ->
                { model with isSaving = None }
        model, Cmd.none
    | ShowMoreNFTs ->
        if model.displayUntil + Config.nftPageSize >= model.maxNFTindex then
            () // ***** TODO ******
        { model with displayUntil = model.displayUntil + Config.nftPageSize }, Cmd.none
    | PlayVideo (a, i) ->
        { model with videoPlaying = Some { tokenAddress = a; tokenId = i; hasStarted = false } }, Cmd.none
    | VideoStarted ->
        match model.videoPlaying with
        | Some videoPlaying -> { model with videoPlaying = Some { videoPlaying with hasStarted = true } }, Cmd.none
        | _ -> model, Cmd.none
    | VideoEnded ->
        { model with videoPlaying = None }, Cmd.none
    | ShowNftDetails (nft, collection) ->
        (Browser.Dom.document.getElementById "body").classList.add("rmodal-open")
        (Browser.Dom.document.getElementById "html").classList.add("rmodal-open")
        { model with nftDetails = Some { nft = nft.nft; collection = collection; likes = [||]; displayReactionBoard = false; likeCount = nft.likeCount }; likeEmojiInput = [] },
            Cmd.batch
                [Navigation.Navigation.newUrl (Global.toUrl (Global.Page.NftDetails (nft.nft.token_address, nft.nft.token_id)))
                 Cmd.OfAsync.perform (fun _ -> Server.getLikes nft.nft.token_address nft.nft.token_id) ()
                    (fun likes -> SetLikesForNftDetails(nft.nft.token_address, nft.nft.token_id, likes))]
    | SetLikesForNftDetails(address, id, likes) ->
        match model.nftDetails with
        | Some nftDetails when nftDetails.nft.token_address = address && nftDetails.nft.token_id = id ->
            { model with nftDetails = Some { nftDetails with likes = likes } }, Cmd.none
        | _ -> model, Cmd.none
    | CloseNftDetails ->
        (Browser.Dom.history.back())
        model, Cmd.none
    | LikeEmojiInput emoji ->
        //let e = Browser.Dom.document.getElementById("emoji-input-element")
        //e?scrollTo(e.scrollWidth, 0)
        { model with likeEmojiInput = emoji::model.likeEmojiInput }, Cmd.none   // scroll to the right)
    | LikeEmojiBackspace ->
        let input =
            match model.likeEmojiInput with
            | [] -> []
            | (_::es) -> es
        { model with likeEmojiInput = input }, Cmd.none
    | ToggleReactionBoard ->
        match model.nftDetails with
        | Some nftDetails ->
            { model with nftDetails = Some { nftDetails with displayReactionBoard = not nftDetails.displayReactionBoard } }, Cmd.none
    | _ ->
        match model.treeEdit with
        | Some tree ->
            let tree = { tree with hasChanges = true }
            match msg with
            | UserInput (field, value) ->
                match field with
                | Name ->
                    { model with treeEdit = Some { tree with nameField = value } }, Cmd.none
                | Bio ->
                    { model with treeEdit = Some { tree with bioField = value } }, Cmd.none
                | LinkName id ->
                    let links = tree.linkFields |> Array.map (fun f ->
                        if f.key = id then { f with nameField = value } else f)
                    { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
                | LinkUrl id ->
                    let links = tree.linkFields |> Array.map (fun f ->
                        if f.key = id then { f with urlField = value } else f)
                    { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
            | ToggleNft (address, id) ->
                match tree.nftFields |> Map.tryFind (address, id) with
                | None ->
                    { model with treeEdit = Some { tree with nftFields = tree.nftFields |> Map.add (address, id) true } }, Cmd.none
                | Some _ ->
                    { model with treeEdit = Some { tree with nftFields = tree.nftFields |> Map.remove (address, id) } }, Cmd.none
            | LinkUp key ->
                let index = tree.linkFields |> Array.findIndex (fun l -> l.key = key)
                let a = tree.linkFields.[index - 1]
                let b = tree.linkFields.[index]
                let links = tree.linkFields |> Array.mapi (fun i l ->
                    match i with
                    | _ when i = index - 1 -> b
                    | _ when i = index -> a
                    | _ -> l)
                { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
            | LinkDown key ->
                let index = tree.linkFields |> Array.findIndex (fun l -> l.key = key)
                let a = tree.linkFields.[index + 1]
                let b = tree.linkFields.[index]
                let links = tree.linkFields |> Array.mapi (fun i l ->
                    match i with
                    | _ when i = index + 1 -> b
                    | _ when i = index -> a
                    | _ -> l)
                { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
            | LinkEdit key ->
                let links = tree.linkFields |> Array.map (fun l -> if l.key = key then { l with editing = true } else l)
                { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
            | LinkCancel key ->
                let links = tree.linkFields |> Array.map (fun l ->
                    if l.key = key then { l with nameField = l.name; urlField = l.url; editing = false } else l)
                { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
            | LinkAccept key ->
                let links = tree.linkFields |> Array.map (fun l -> if l.key = key then { l with name = l.nameField; url = l.urlField; editing = false } else l)
                { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
            | LinkAdd ->
                let links = Array.append tree.linkFields [| { key = System.Guid.NewGuid().ToString(); name = ""; url = ""; nameField = ""; urlField = ""; editing = true; index = -1 } |]
                { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
            | LinkDelete key ->
                let links = tree.linkFields |> Array.choose (fun l -> if l.key = key then None else Some l)
                { model with treeEdit = Some { tree with linkFields = links } }, Cmd.none
            | MakeProfileNft (address, id) ->
                let nft =
                    match model.nfts with
                    | Some nfts ->
                        nfts.results
                            |> Array.collect (fun c -> c.nfts)
                            |> Array.tryFind (fun n -> n.nft.token_address.ToLower() = address && n.nft.token_id = id)
                            |> Option.map (fun n -> n.nft)
                    | _ -> None
                { model with profileNft = nft; treeEdit = Some { tree with profileNft = Some (address, id) } }, Cmd.none
            | SelectColors colors ->
                { model with treeEdit = Some { tree with colors = colors } }, Cmd.none
            | SetThemeMode darkMode ->
                { model with treeEdit = Some { tree with darkMode = darkMode } }, Cmd.none
