module App.View

open Elmish
open Elmish.Navigation
open Elmish.UrlParser
open Fable.Core
open Fable.Core.JsInterop
open Types
open App.State
open Global
open Imports

importAll "../sass/main.sass"

open Fable.React
open Fable.React.Props

let [<Global("window")>] window: obj = jsNative

let web3ModalConfig =
    keyValueList CaseRules.LowerFirst
        [!!("network", "mainnet")
         !!("cacheProvider", true)
         !!("providerOptions", keyValueList CaseRules.LowerFirst [])]

let web3Modal = new Web3Modal(web3ModalConfig)

let root model dispatch = 

  let pageHtml page =
    match page with
    | Home -> Home.View.root model.home model.common (HomeMsg >> dispatch)
    | View _ | Edit _ -> New.View.root model.tree model.common (NewMsg >> dispatch)

  div
    []
    [pageHtml model.page]

open Elmish.React
open Elmish.Debug
open Elmish.HMR

let gbl =
    let web3 = new Web3(new Web3WsProvider(Common.Config.network.infuraWsUrl))
    let contract = web3.NewContract(Common.web3treeAbi, Common.Config.network.web3treeContractAddress)
    { web3Modal = web3Modal
      window = window
      web3 = web3
      web3treeContract = contract}

let metamaskChangeAccount _ =
    Cmd.ofSub (fun dispatch ->
        if window?ethereum <> null then
            window?ethereum?on("accountsChanged", fun _ -> dispatch RefreshAccounts))

// App
Program.mkProgram (init gbl) (update gbl) root
|> Program.withSubscription metamaskChangeAccount
|> Program.toNavigable (parsePath pageParser) (urlUpdate gbl)
#if DEBUG
|> Program.withDebugger
#endif
|> Program.withReactBatched "elmish-app"
|> Program.run
