    let getProfileEvents (account :string) =
        let url =
            "https://eqvine8lhmho.usemoralis.com:2053/server/classes/profile?order=-block_number&limit=1&where=%7B%20%22user%22%20%3A%20%22" + account.ToLower() + "%22%20%7D"
        async {
            let! response =
                Http.request url
                    |> Http.method GET
                    |> Http.header (Headers.accept "application/json")
                    |> Http.header (Headers.create "X-Parse-Application-Id" "xM24pQrlxdF6qlE6cTNfb1vvztGUCRV98APSyytQ")
                    |> Http.header (Headers.create "X-Parse-Master-Key" "mMnh4gsodOoq9QTwG8l9uAW2fmcDNHDRbQFiXdqG")
                    |> Http.send
            let results : EventResults<ProfileEvent> = Json.parseAs response.responseText
            return results
            }

module Server =

    open Fable.SimpleJson
    open Fable.SimpleHttp

    let postReceipt (receipt :obj) =
        async {
            let body = Json.stringify receipt
            let! code, _ = Http.post "https://web3tree.azurewebsites.net/api/receipt" body
            match code with
            | 200 -> return ()
            | _ -> return failwith "error"
        }

    let getLikes tokenAddress tokenId :Like[] Async =
        async {
            let! code, response = Http.get <| sprintf "https://web3tree.azurewebsites.net/api/likes/%s/%s" tokenAddress tokenId
            match code with
            | 200 -> return Json.parseAs response
            | _ -> return [||]
        }

    let getProfileInfo address :ProfileInfo option Async =
        async {
            let! code, response = Http.get <| "https://web3tree.azurewebsites.net/api/profileinfo/" + address
            match code with
            | 200 -> return Some (Json.parseAs response)
            | 404 -> return None
            | _ -> return failwith "error"
        }

    let getAddress username =
        async {
            let! code, response = Http.get <| "https://web3tree.azurewebsites.net/api/address/" + username
            match code with
            | 200 -> return response
            | _ -> return failwith "error"
        }

    let getEns address =
        async {
            let! code, response = Http.get <| "https://web3tree.azurewebsites.net/api/ens/" + address
            match code with
            | 200 when not <| System.String.IsNullOrEmpty response -> return Some response
            | _ -> return None
        }

    let getTreeData address =
        async {
            let! response = Moralis.getProfileEvents address
            match response.results.Length with
            | 0 ->
                // pick colour scheme at random from address
                let r = System.Int32.Parse(address.Substring(2, 2), System.Globalization.NumberStyles.HexNumber)
                let colors, _ = ColorUtils.gradients |> List.item (r % ColorUtils.gradients.Length)
                let darkMode = r % 2 = 0
                return
                    { TreeData.name = ""
                      profileNft = None
                      bio = ""
                      links = [||]
                      nfts = Map.empty
                      colors = colors |> List.toArray
                      darkMode = darkMode }
            | _ ->
                let latest = response.results |> Array.maxBy (fun e -> e.block_number)
                return latest.text |> TreeData.fromMarkdown
        }

    let getNFTs account =
        async {
            let! code, response = Http.get <| "https://web3tree.azurewebsites.net/api/nfts?account=" + account
            match code with
            | 200 ->
                let nfts :PagedResult<NFTCollectionResult> = Json.parseNativeAs response
                return nfts
            | _ -> return { results = [||]; cursor = null }
        }

    let getNFT tokenAddress tokenId =
        async {
            let! code, response = Http.get <| sprintf "https://web3tree.azurewebsites.net/api/nft/%s/%s" tokenAddress tokenId
            match code with
            | 200 when not <| System.String.IsNullOrEmpty (response) ->
                let nft :NFT = Json.parseNativeAs response
                return Some nft
            | _ -> return None
        }


