module Validate =

    let linkName txt = if System.String.IsNullOrEmpty txt then false else true

    let linkUrl txt = System.Text.RegularExpressions.Regex.IsMatch(txt, "^(ht|f)tp(s?)\:\/\/[0-9a-zA-Z]([-.\w]*[0-9a-zA-Z])*(:(0-9)*)*(\/?)([a-zA-Z0-9\-\.\?\,\'\/\\\+&%\$#_]*)?$")

let backgroundColor = 246, 245, 249//25, 23, 40//246, 245, 249

let backgroundGradient color1 color2 backgroundColor isDarkMode =
    let linearGradient direction color1 start1 color2 start2 =
        let (r1, g1, b1, a1) = color1
        let (r2, g2, b2, a2) = color2
        sprintf "linear-gradient(%s, rgba(%i, %i, %i, %f) %f%%, rgba(%i, %i, %i, %f) %f%%)"
            direction r1 g1 b1 a1 start1 r2 g2 b2 a2 start2
    let radialGradient (x, y) color1 start1 color2 start2 =
        let (r1, g1, b1, a1) = color1
        let (r2, g2, b2, a2) = color2
        sprintf "radial-gradient(ellipse at %f%% %f%%, rgba(%i, %i, %i, %f) %f%%, rgba(%i, %i, %i, %f) %f%%)"
            x y r1 g1 b1 a1 start1 r2 g2 b2 a2 start2
    let (rback, gback, bback) = backgroundColor
    let (r1, g1, b1) = color1
    let (r2, g2, b2) = color2
    let fadeBottom = linearGradient "to bottom" (rback, gback, bback, 1.0) 0.0 (255, 255, 255, 0.0) 90.0
    let fadeTop = linearGradient "to top" (rback, gback, bback, 1.0) 0.0 (255, 255, 255, 0.0) 90.0
    let radBotRight = radialGradient (100.0, 75.0) (r1, g1, b1, 0.4) 0.0 (rback, gback, bback, 0.3) 50.0
    let radTopLeft = radialGradient (0.0, 25.0) (r2, g2, b2, 0.65) 0.0 (rback, gback, bback, 0.3) 50.0
    let backgroundColorStr = sprintf "rgb(%i, %i, %i)" rback gback bback
    let centerFade = if isDarkMode then "rgba(22, 22, 22, 1.0)" else "rgba(244, 244, 244, 1.0)"
    [BackgroundSize "100vw 100vh"; BackgroundColor backgroundColorStr;BackgroundImage (sprintf "linear-gradient(to right, rgba(0, 0, 0, 0) 25%%, %s 50%%, rgba(0, 0, 0, 0) 75%%), %s, %s, %s, %s" centerFade fadeBottom fadeTop radBotRight radTopLeft); BackgroundBlendMode "normal, normal, normal, normal, normal"]

let getLinkGradient (hexColors :string[]) =
    sprintf "linear-gradient(to right, %s)" (System.String.Join(", ", hexColors))

let emoji code =
    img [Src <| "/img/emojis/png/72/" + code + ".png"]

let getMimeType format =
    match format with
    | "mp4" -> "video/mp4"
    | _ -> "unknown"

let gradientSelector dispatch =
    div [ClassName "theme-selector"] 
        [
         div [ClassName "gradient-selector"]
            (ColorUtils.gradients |> List.map (fun (colors, name) ->
                let carray = colors |> List.toArray
                let r = System.Int32.Parse(colors.[0].Substring(2, 2), System.Globalization.NumberStyles.HexNumber)
                let gradient = sprintf "linear-gradient(to right, %s)" (System.String.Join(", ", colors))
                let style =
                    match ColorUtils.isLight (colors :> string seq) with
                    | true -> [Background gradient; Color "black"]
                    | _ -> [Background gradient]
                div [OnClick (fun _ -> dispatch <| SelectColors carray);Style style] [str name]))
         div [ClassName "mode"] [
             div [OnClick (fun _ -> dispatch <| SetThemeMode true)] [i [ClassName "mdi mdi-weather-night"] []]
             div [OnClick (fun _ -> dispatch <| SetThemeMode false)] [i [ClassName "mdi mdi-weather-sunny"] []]]]

let nftCollectionBadge (collection :NFTCollectionResult) =
    let getBadgeClass n =
        match n with
        | 0 | 1 -> ""
        | 2 -> "bronze"
        | 3 -> "silver"
        | _ -> "gold"
    div [ClassName "nft-collection-badge"]
        [img [Src collection.collection.imageUrl]
         if collection.nfts.Length > 1 then
            div [ClassName <| getBadgeClass collection.nfts.Length] [span [] [str <| collection.nfts.Length.ToString()]]
         else ofOption None]

let nftCollectionBadges collections =
    div [ClassName "nft-collection-badges"]
        [ofList (collections |> Array.map nftCollectionBadge |> Array.toList)]

let like (like :Like) =
    let emojis =
        like.text.Split(' ') |> Array.choose (fun name ->
            Emojis.charByName |> Map.tryFind name |> Option.map (fun c -> span [ClassName <| "emoji-" + name] [str c]))
    let emojis = if emojis.Length > 3 then emojis |> Array.take 3 else emojis
    let display =
        if System.String.IsNullOrEmpty like.userDisplay then Utils.niceAddress like.userAddress else like.userDisplay
    div [ClassName "nft-comment mode-aware-border"]
        [div [ClassName "profile-image"]
            [if System.String.IsNullOrEmpty like.userImage then
                let seed = System.Int32.Parse(like.userAddress.Substring(2, 8), NumberStyles.HexNumber)
                jazzicon [Diameter 48; Seed seed] []
             else
                img [Src like.userImage]]
         div []
            [div [ClassName "name"] [str display]
             div [ClassName "content"] [div [] emojis]
             div [ClassName "block mode-aware-text2"] [span [ClassName "icon"] [i [ClassName "mdi mdi-ethereum"] []]; span [] [str <| "Block: " + like.blockNumber.ToString()]]
             div [ClassName "time"] [str <| Utils.niceTimestamp like.timestamp]]]

let emojiBoard common model dispatch =
    let disabled =
        match common.accountData with
        | None -> true
        | Some _ -> false
    let postDisabled =
        match common.accountData, model.likeEmojiInput with
        | _, [] | None, _ -> true
        | _ -> false
    div [ClassName "emoji-board"]
        [div [ClassName "emoji-board-input"]
            [div [] [str ""]
             div [Id "emoji-input-element"] [
                div [] [
                    // placeholder for scrolling hack
                    if model.likeEmojiInput.IsEmpty then ofOption None else span [Id "emoji-placeholder"] [str "x"]
                    match model.likeEmojiInput with
                    | [] -> str "Add Emoji Reaction"
                    | es ->
                        es |> List.map (fun e ->
                            let code = Emojis.picByName |> Map.find e
                            emoji code) |> ofList
                    ]]]
         div [ClassName "emoji-board-keys"]
            [(Emojis.emojis |> List.map (fun (name, emoji, _) ->
                button [Disabled disabled; OnClick (fun _ -> dispatch <| LikeEmojiInput name)] [img [Src <| "/img/emojis/png/72/" + emoji + ".png"]])) |> ofList
             button [Disabled disabled; OnClick (fun _ -> dispatch LikeEmojiBackspace)] [i [ClassName "mdi mdi-backspace-outline"] []]]
         match common.accountData with
         | Some _ ->
             div []
                [button [OnClick (fun _ -> dispatch <| MakeTransaction PostLike); Disabled postDisabled; ClassName "button mode-aware-border"] [b [] [str "Post"]]]
         | None ->
            div []
                [button [ClassName "button"; OnClick <| fun _ -> dispatch Connect] [str "Connect Account To React"]]
        ]

let reactionBoard common model dispatch =
    let disabled =
        match common.accountData, model.likeEmojiInput with
        | None, _ | _ , [_; _; _] -> true
        | _ -> false
    let backDisabled =
        match common.accountData, model.likeEmojiInput with
        | None, _ | _ , [] -> true
        | _ -> false
    let postDisabled =
        match common.accountData, model.likeEmojiInput with
        | _, [] | None, _ -> true
        | _ -> false
    div []
       [div [ClassName "reaction-input"]
            [match model.likeEmojiInput with
             | [] -> div [ClassName "mode-aware-text2"] [str "Add Emoji Reaction"]
             | es ->
               es |> List.map (fun e ->
                   let c = Emojis.charByName |> Map.find e
                   div [] [span [ClassName ("emoji emoji-" + e)] [str c]]) |> ofList]
        div [ClassName "reaction-board"]
            [(Emojis.emojis |> List.map (fun (emoji, _, char) ->
                button [Disabled disabled; OnClick (fun _ -> dispatch <| LikeEmojiInput emoji); ClassName <| "emoji-" + emoji] [span [] [str char]])) |> ofList
             button [Disabled backDisabled; OnClick (fun _ -> dispatch LikeEmojiBackspace)] [i [ClassName "mdi mdi-backspace-outline"] []]
             ]
        match common.accountData with
        | Some _ ->
            div []
               [button [OnClick (fun _ -> dispatch <| MakeTransaction PostLike); Disabled postDisabled; ClassName "button mode-aware-border"] [b [] [str "Post"]]]
        | None ->
           div []
               [button [ClassName "button"; OnClick <| fun _ -> dispatch Connect] [str "Connect Account To React"]]
       ]

let nftModal (nftDetails :NftDetails) style common model dispatch =
    let likeCountTxt =
        match nftDetails.likeCount with
        | 1 -> " reaction"
        | _ -> " reactions"
    div [ClassName "rmodal"]
        [div [ClassName "rmodal-close"]
            [button [OnClick (fun _ -> dispatch CloseNftDetails)] [i [ClassName "mdi mdi-close mdi-24px"] []]]
         div [ClassName "rmodal-left"]
            [div [ClassName "rmodal-media"]
                [match nftDetails.nft.display with
                 | { display = "model"; url = src } ->
                    modelViewer [Src src; HTMLAttr.Custom ("bounds", "tight"); HTMLAttr.Custom("enable-pan", true); HTMLAttr.Custom("camera-controls", "true"); HTMLAttr.Custom("shadow-intensity", 2); HTMLAttr.Custom("shadow-softness", 0.65); HTMLAttr.Custom ("ar-modes", "webxr scene-viewer quick-look"); HTMLAttr.Custom ("exposure", 1); HTMLAttr.Custom("environment-image", "neutral")] []
                 | { display = "image"; url = src } ->
                    img [Src src]
                 | { display = "video"; url = src; format = format } ->
                    video [AutoPlay false; Controls true]
                        [source [Src src; Type <| getMimeType format]]
                 | _ -> ofOption None]]
         div [ClassName "rmodal-right"; Style style]
            [h1 [] [str nftDetails.collection.name]
             h2 [ClassName "mode-aware-border"] [str nftDetails.nft.title]
             div [classBaseList "nft-comments-summary mode-aware-border" ["active", nftDetails.displayReactionBoard]]
                [p [] [a [OnClick (fun _ -> dispatch ToggleReactionBoard)] [span [ClassName "emoji"] [str ""]; br[];  b [] [str <| nftDetails.likeCount.ToString() + likeCountTxt]]]
                 reactionBoard common model dispatch] //emojiBoard common model dispatch
             div [ClassName "nft-comments-full mode-aware-border"]
                (nftDetails.likes |> Array.map like |> Array.toList)
              ]]

let nfts (model :Model) tree dispatch =
    let getFormatName =
        function
        | "svg" -> "SVG"
        | "png" -> "PNG"
        | "gif" -> "GIF"
        | "jpg" -> "JPEG"
        | "webp" -> "WebP"
        | _ -> "Image"
    let getStorageName =
        function
        | "onchain" -> "On-chain"
        | "ipfs" -> "IPFS"
        | _ -> "Server"
    let getVideoFormatName =
        function
        | "mp4" -> "MP4"
        | _ -> "Video"
    let getMediaText =
        function
        | { display = "image"; storage = "onchain" } ->
            "On-chain Image"
        | { display = "image"; format = format; storage = storage } -> (getFormatName format) + " on " + (getStorageName storage)
        | { display = "video"; format = format; storage = storage } -> (getVideoFormatName format) + " on " + (getStorageName storage)
        | _ -> ""
    let getMediaElement display =
        let txt = getMediaText display
        match display with
        | { display = "image"; storage = "onchain" } ->
            [span [ClassName "icon on-chain is-small"] [i [ClassName "mdi mdi-vector-square"] []]; span [] [str txt]]
        | { storage = "ipfs" } | { storage = "ipfs" } ->
            [span [ClassName "icon ipfs is-small"] [i [ClassName "mdi mdi-earth"] []]; span [] [str txt]]
        | _ -> [span [] [str txt]]
    let shouldDisplay (nft :NFT) =
        match model.treeEdit with
        | Some _ -> true
        | _ -> not (tree.nfts |> Map.containsKey (nft.token_address, nft.token_id))
    let classes (nft: NFT) =
        match model.treeEdit with
        | Some tree when tree.nftFields |> Map.containsKey (nft.token_address, nft.token_id) ->
            ["hidden", true]
        | _ -> []
    match model.nfts with
    | None -> div [ClassName "lds-dual-ring"] []
    | Some nfts ->
        div [ClassName "gallery"]
            (nfts.results |> Array.map (fun collectionResult ->
                let collection = collectionResult.collection
                if collectionResult.nfts.[0].index < model.displayUntil then
                    div [ClassName "collection"]
                        [div [ClassName "collection-spacer mode-aware-border"] [str ""]
                         div [ClassName "collection-heading"]
                            [if System.String.IsNullOrEmpty collection.bannerUrl || System.String.IsNullOrEmpty collection.imageUrl then ofOption None
                             else
                                div [ClassName "banner-image"; Style [BackgroundImage ("url(" + collection.bannerUrl + ")")]] []
                                div [ClassName "collection-image"; Style [BackgroundImage ("url(" + collection.imageUrl + ")")]] []
                             h1 [] [str collection.name]]
                         div [ClassName "collection-description"]
                            [ofOption
                                (if System.String.IsNullOrEmpty(collection.description) then
                                    None
                                 else
                                    Some <|
                                        h2 [] [markdown [str collection.description]])]
                         div [ClassName "nfts"]
                            (collectionResult.nfts |> Array.map (fun nftResult ->
                            let nft = nftResult.nft
                            if nftResult.index < model.displayUntil then
                                match shouldDisplay nft with
                                | false -> ofOption None
                                | true ->
                                    match nft.display with
                                    | { display = "image" } | { display = "video" } | { display = "unknown" } | { display = "text" } | { display = "model" }->
                                        div [ClassName "nft"]
                                            [// MEDIA
                                             match nft.display with
                                             | { display = "model"; url = src; format = "glb" } ->
                                                modelViewer [OnClick (fun _ -> dispatch <| ShowNftDetails (nftResult, collection)); Src src; HTMLAttr.Custom ("bounds", "tight"); HTMLAttr.Custom("enable-pan", true); HTMLAttr.Custom("auto-rotate", "true"); HTMLAttr.Custom("shadow-intensity", 2); HTMLAttr.Custom("shadow-softness", 0.65); HTMLAttr.Custom ("ar-modes", "webxr scene-viewer quick-look"); HTMLAttr.Custom ("exposure", 1); HTMLAttr.Custom("environment-image", "neutral")] []
                                             | { display = "text"; url = src; format = "html" } ->
                                                // web page
                                                iframe [Src src; HTMLAttr.Width "340"; HTMLAttr.Height "550"] []
                                             | { display = "image"; url = src } | { display = "unknown"; url = src } ->
                                                // static image
                                                img [OnClick (fun _ -> dispatch <| ShowNftDetails (nftResult, collection)); classList <| classes nft; Src src]
                                             | { display = "video"; previewUrl = previewUrl; url = src; format = format }
                                                when not <| System.String.IsNullOrEmpty previewUrl ->
                                                    // video preview image
                                                    let playing, started =
                                                        match model.videoPlaying with
                                                        | Some { tokenAddress = token_address; tokenId = token_id; hasStarted = hasStarted }
                                                           when token_address = nft.token_address && token_id = nft.token_id -> true, hasStarted
                                                        | _ -> false, false
                                                    div [ClassName "video-preview"] 
                                                        [img [OnClick (fun _ -> dispatch <| ShowNftDetails (nftResult, collection)); classList <| classes nft; Src previewUrl]
                                                         (match playing with
                                                          // video with preview but playing
                                                          | true ->
                                                                video [OnEnded(fun _ -> dispatch VideoEnded); OnPause(fun _ -> dispatch VideoEnded); OnPlay(fun _ -> dispatch VideoStarted); classList ["started", started];AutoPlay true; Muted isMobile]
                                                                   [source [Src src; Type <| getMimeType format]]
                                                          | _ -> ofOption None)
                                                         let cls = ["video-controls", true; "playing", started]
                                                         div [classList cls] 
                                                            [div [ClassName "video-controls-inner"]
                                                                [a [ClassName "video-stop"; OnClick (fun _ -> dispatch <| VideoEnded)] [i [ClassName "mdi mdi-stop"] []]
                                                                 a [ClassName "video-play"; OnClick (fun _ -> dispatch <| PlayVideo (nft.token_address, nft.token_id))] [i [ClassName "mdi mdi-play"] []]]]]
                                             | { display = "video"; url = src; format = format } ->
                                                // video without preview
                                                video [classList <| classes nft; AutoPlay false; Controls true]
                                                    [source [Src src; Type <| getMimeType format]]
                                             // EDIT CONTROLS
                                             match model.treeEdit with
                                             | None -> ofOption None
                                             | Some tree ->
                                                 div [ClassName "edit-nft"]
                                                    [button [ClassName "button"; OnClick (fun _ -> dispatch <| ToggleNft (nft.token_address, nft.token_id) )] [
                                                        match tree.nftFields |> Map.tryFind (nft.token_address, nft.token_id) with
                                                        | None ->
                                                            span [ClassName "icon"] [i [ClassName "mdi mdi-eye-off"] []]; span [] [str "Hide"]
                                                        | Some _ ->
                                                            span [ClassName "icon"] [i [ClassName "mdi mdi-eye"] []; span [] [str "Show"]]]
                                                     match nft.display.display with
                                                     | "image" ->
                                                         button [ClassName "button"; OnClick (fun _ -> dispatch <| MakeProfileNft (nft.token_address, nft.token_id)); Disabled (tree.profileNft = Some (nft.token_address, nft.token_id))]
                                                            [str "Set Profile"]
                                                     | _ -> ofOption None]
                                             // CARD
                                             div [ClassName "nft-card mode-aware-border"] [
                                                p [ClassName "nft-title"] [str nft.title]
                                                p [ClassName "nft-media"] (getMediaElement nft.display)
                                                ofOption <|
                                                    if System.String.IsNullOrEmpty(collection.description) && not <| System.String.IsNullOrEmpty(nft.description) then
                                                        Some <| p [ClassName "nft-desc"] [str nft.description]
                                                    else None
                                                p [ClassName "nft-comments"]
                                                    [match nftResult.likeCount with
                                                     | 0 ->
                                                        a [OnClick (fun _ -> dispatch <| ShowNftDetails (nftResult, collection))] [b [] [span [ClassName "emoji"] [str ""]; str "No reactions"]]
                                                     | n ->
                                                       [p [] [a [OnClick (fun _ -> dispatch <| ShowNftDetails (nftResult, collection))] [span [ClassName "emoji"] [str ""]; b [] [str <| n.ToString()]]]
                                                        nftResult.likes |> Array.map (fun like ->
                                                            let emojis = like.text.Split(' ')
                                                            let emojis = if emojis.Length > 3 then emojis |> Array.take 3 else emojis
                                                            let emojis = emojis |> Array.choose (fun e ->
                                                                Emojis.charByName |> Map.tryFind e |> Option.map (fun c -> span [ClassName <| "emoji emoji-" + e] [str c])) |> Array.toList
                                                            p [ClassName "reaction"] emojis) |> ofArray
                                                        a [OnClick (fun _ -> dispatch <| ShowNftDetails (nftResult, collection))] [i [ClassName "mdi mdi-dots-horizontal"] []]] |> ofList
                                                     ]]]
                                    | _ -> ofOption None
                            else ofOption None ))]
                else ofOption None))

let footerElement =
    footer [ClassName "footer"]
        [a [Href "/"]
            [img [Src "/img/tree.svg"] 
             h1 [] [str "Web3 Tree"]]]

let editBar model common dispatch =
    let isSaving =
        match model.isSaving with
        | Some Save -> true
        | _ -> false
    match model.treeEdit, common.accountData, model.address with
    | Some tree, Some account, Some address when account.selectedAccount.ToLower() = address.ToLower() && account.chainId = Config.network.chainId ->
        nav [ClassName "navbar edit is-fixed-top"]
            [div [ClassName "navbar-brand"]
                [div [ClassName "navbar-item"] [span [ClassName "icon"] [i [ClassName "mdi mdi-cog-outline"] []]; span [] [str "Editing"]]]
             div [ClassName "navbar-item controls"]
                [button [ClassName "button save-button"; Disabled ((not tree.hasChanges) || isSaving); OnClick (fun _ -> dispatch <| MakeTransaction Save)] [span [ClassName "icon"] [i [ClassName "mdi mdi-content-save-outline"] []]; span [] [str "Save"]]
                 ]]
    | Some _, Some account, Some address ->
        let errTxt =
            if account.selectedAccount.ToLower() <> address.ToLower() then "Connected to wrong account to edit" else "Connected to wrong chain"
        nav [ClassName "navbar edit is-fixed-top"]
            [div [ClassName "navbar-brand"]
                [div [ClassName "navbar-item error"] [span [ClassName "icon"] [i [ClassName "mdi mdi-alert-octagon"] []]; span [] [str errTxt]]]]
    | _ -> failwith "invalid model state"

let editLink dispatch displayEditBtns (tree :TreeEditData) n index (link :TreeEditLink) =
    let valid = Validate.linkUrl link.urlField && Validate.linkName link.nameField
    let revertDeletes = link.name = "" && link.url = ""
    let style =
        match link.editing with
        | true -> []
        | _ when tree.colors |> ColorUtils.isLight ->
            [Background (getLinkGradient (tree.colors) :> obj); Color "black"; Border "2px solid #444"]
        | _ -> [Background (getLinkGradient (tree.colors) :> obj)]
    div [classList ["editing", link.editing; "field", true; "edit-link", true]; Style style]
        [div [ClassName "name"] [str link.name]
         div [ClassName "control"]
            (match link.editing with
             | true ->
                let nameValid = link.name <> link.nameField && Validate.linkName link.nameField
                let nameInvalid = link.name <> link.nameField && not <| Validate.linkName link.nameField
                let urlValid = link.url <> link.urlField && Validate.linkUrl link.urlField
                let urlInvalid = link.url <> link.urlField && not <| Validate.linkUrl link.urlField
                [input [Id link.key; classList ["input", true ;"link-name", true; "valid", nameValid; "invalid", nameInvalid]; Type "text"; Placeholder "Title"; DefaultValue link.nameField; OnChange (fun e -> UserInput(LinkName link.key, e.target?value) |> dispatch )]
                 input [classList ["input link-name", true; "valid", urlValid; "invalid", urlInvalid]; Type "text"; Placeholder "Url"; DefaultValue link.urlField; OnChange (fun e -> UserInput(LinkUrl link.key, e.target?value) |> dispatch )] ]
             | false -> [])
         div [ClassName "control-buttons"]
            [if link.editing then button [ClassName "control-cancel"; OnClick (fun _ -> dispatch <| (if revertDeletes then LinkDelete else LinkCancel) link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-arrow-u-left-top-bold mdi-24px"] []]] else ofOption None
             if link.editing then button [Disabled <| not valid; ClassName "control-accept"; OnClick (fun _ -> dispatch <| LinkAccept link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-check-outline mdi-24px"] []]] else ofOption None
             if displayEditBtns then button [ClassName "control-edit"; OnClick (fun _ -> dispatch <| LinkEdit link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-square-edit-outline mdi-24px"] []]] else ofOption None
             if index = 0 || link.editing then ofOption None else button [ClassName "control-up"; OnClick (fun _ -> dispatch <| LinkUp link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-arrow-up-box mdi-24px"] []]]
             if index = n - 1 || link.editing then ofOption None else button [ClassName "control-down"; OnClick (fun _ -> dispatch <| LinkDown link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-arrow-down-box mdi-24px"] []]]
             if link.editing then ofOption None else button [ClassName "control-delete"; OnClick (fun _ -> dispatch <| LinkDelete link.key)] [span [ClassName "icon"] [i [ClassName "mdi mdi-delete mdi-24px"] []]] ]]

