let link style (link :TreeLink) =
    let icon =
        match link.url with
        | url when url.Contains("instagram.com") -> i [ClassName "mdi mdi-instagram"] []
        | url when url.Contains("twitter.com") -> i [ClassName "mdi mdi-twitter"] []
        | url when url.Contains("facebook.com") -> i [ClassName "mdi mdi-facebook"] []
        | url when url.Contains("discord.com") -> i [ClassName "mdi mdi-discord"] []
        | _ -> ofOption None
    a [ClassName "button"; Href link.url; Style style]
        [icon; str link.name]

let tree model common dispatch =
    match model.mode, common.accountData with
    | Edit, None -> [div [ClassName "connect-info"] [p [] [str "Connect your account to edit your Web3Tree"]; button [ClassName "button connect-button dark"; OnClick (fun _ -> dispatch Connect)] [str "Connect Account"]]]
    | _ ->
        match model.tree, model.nfts, model.address with
        | None, _, _ | _, None, _ -> [div [ClassName "lds-dual-ring big-loading"] []]
        | Some tree, Some _, Some address ->
                let seed = System.Int32.Parse(address.Substring(2, 8), NumberStyles.HexNumber)
                [   // HEADER SECTION
                    div [ClassName "profile-info"]
                       [// PROFILE PIC
                        match model.treeEdit, tree.profileNft with
                        | None, None | Some { profileNft = None }, _ ->
                            jazzicon [Diameter 100; Seed seed] []
                        | _ ->
                            match model.profileNft with
                            | None -> ofOption None
                            | Some nft ->
                                div [ClassName "image-outer"]
                                    [div [ClassName "image"; Style [BackgroundImage ("url(" + nft.display.url + ")")]] []]
                        // HEADER
                        match model.treeEdit with
                        | Some tree -> editName tree dispatch
                        | None ->
                           h1 [ClassName "mode-aware-text"] [
                              match tree.name with
                              | "" ->
                                    match model.ens with
                                    | None -> str model.address.Value
                                    | Some name -> str name
                              | name -> str name]
                        // BIO
                        match model.treeEdit with
                        | Some tree -> editBio tree dispatch
                        | None ->
                            p [] [str tree.bio]
                        ]
                    // LINKS
                    match model.treeEdit, tree.links with
                    | None, [||] -> ofOption None
                    | None, _ ->
                        let style =
                            match tree.colors with
                            | [||] -> []
                            | _ when tree.colors |> ColorUtils.isLight ->
                                [Background (getLinkGradient (tree.colors) :> obj); Color "black"; Border "2px solid #444"]
                            | _ -> [Background (getLinkGradient (tree.colors) :> obj)]
                        ofList <|
                            [
                             div [ClassName "links"] (tree.links |> Array.map (link style) |> Array.toList)]
                    | Some etree, _ ->
                        let displayEditBns = etree.linkFields |> Array.exists (fun l -> l.editing) |> not
                        ofList <|
                            [
                             div [ClassName "links edit-links"] (etree.linkFields |> Array.mapi (fun i elink -> editLink dispatch displayEditBns etree etree.linkFields.Length i elink) |> Array.toList)
                             button [Disabled <| not displayEditBns;ClassName "add-link button"; OnClick (fun _ -> dispatch LinkAdd)]
                                [span [ClassName "icon"] [i [ClassName "mdi mdi-link-plus"] []]; span [] [str "Add Link"]]
                             gradientSelector dispatch]
                    // NFTS
                    match model.mode, model.tree, model.nfts with
                    | View, Some { links = [||] }, Some { results = [||]; cursor = null } ->
                        // empty tree!
                        div [ClassName "empty-tree"] [i [ClassName "mdi mdi-tree-outline"] []]
                    | _, _, Some { results = [||] } | _, None, Some { results = [||] } | _, _, None -> ofOption None
                    | _, _, Some nftResults ->
                        div [ClassName "section-header"] [div [ClassName "section-line mode-aware-border"] []; h1 [ClassName "mode-aware-text"] [i [ClassName "mdi mdi-dots-grid"] []; span [] [str "Collections"]]; div [ClassName "section-line mode-aware-border"] []]
                        nftCollectionBadges nftResults.results
                        div [ClassName "section-header"] [div [ClassName "section-line mode-aware-border"] []; h1 [ClassName "mode-aware-text"] [i [ClassName "mdi mdi-dots-grid"] []; span [] [str "NFT Gallery"]]; div [ClassName "section-line mode-aware-border"] []]
                        infiniteScroll [LoadMore (fun _ -> dispatch ShowMoreNFTs); HasMore (Some (model.maxNFTindex > model.displayUntil))]
                            [nfts model tree dispatch]]

let root model common dispatch =
    let hasbar =
        match model.mode, model.treeEdit, common.accountData with
        | Edit, Some _, Some _ -> true
        | _ -> false
    let colors =
        match model.tree, model.treeEdit with
        | _, Some tree -> tree.colors
        | Some tree, _ -> tree.colors
        | _ -> [||]
    let darkMode =
        match model.tree, model.treeEdit with
        | _, Some tree -> tree.darkMode
        | Some tree, _ -> tree.darkMode
        | _ -> false
    let mainContentStyle =
        match colors with
        | [||] -> []
        | colors ->
            let scolor = colors.[0]
            let ecolor = colors |> Array.last
            let (bh, bs, _) = ecolor |> ColorUtils.fromHexStr |> ColorUtils.fromRgb
            let backgroundColor = ColorUtils.toRgb (bh, bs * 0.3, if darkMode then 0.1 else 0.97)
            backgroundGradient (ColorUtils.fromHexStr ecolor) (ColorUtils.fromHexStr scolor) backgroundColor darkMode
    let borderStyle =
        match colors |> Array.tryHead with
        | None -> ofOption None
        | Some color ->
            let (h, s, _) = color |> ColorUtils.fromHexStr |> ColorUtils.fromRgb
            let r, g, b = ColorUtils.toRgb (h, s * 0.8, if darkMode then 0.85 else 0.15)
            style [] [str (sprintf ".mode-aware-border { border-color: rgb(%i, %i, %i, 0.2) !important }" r g b)]
    let textStyle =
        match colors |> Array.tryHead with
        | None -> ofOption None
        | Some color ->
            let (h, s, _) = color |> ColorUtils.fromHexStr |> ColorUtils.fromRgb
            let r, g, b = ColorUtils.toRgb (h, s * 0.7, if darkMode then 0.85 else 0.25)
            let r2, g2, b2 = ColorUtils.toRgb (h, s * 0.15, if darkMode then 0.45 else 0.55)
            let r3, g3, b3 = ColorUtils.toRgb(h, s * 0.24, if darkMode then 0.65 else 0.35)
            let (h2, s2, _) = colors |> Array.last |> ColorUtils.fromHexStr |> ColorUtils.fromRgb
            let r4, g4, b4 = ColorUtils.toRgb(h2, s2, if darkMode then 0.75 else 0.35)
            style [] [str (sprintf ".mode-aware-text { color: rgb(%i, %i, %i) !important } .mode-aware-text2 { color: rgb(%i, %i, %i) !important } strong { color: rgb(%i, %i, %i) !important  } a { color: rgb(%i, %i, %i) }" r g b r2 g2 b2 r3 g3 b3 r4 g4 b4)]
    div [classList ["has-text-centered", true; "tree", true; "has-bar", hasbar; "dark-mode", darkMode]]
        [borderStyle
         textStyle
         // NFT DETAILS MODAL
         match model.nftDetails with
         | None -> ofOption None
         | Some nftDetails -> nftModal nftDetails mainContentStyle common model dispatch
         // EDIT BAR
         ofOption <|
         match model.treeEdit, common.accountData with
         | Some _, Some _ -> Some <| editBar model common dispatch
         | _ -> None
         div [classBaseList "main-content" ["dark-mode", darkMode];Style mainContentStyle] [ofList <| tree model common dispatch]
         match model.isSaving with
         | Some transType ->
            let accountName =
                match common.accountData.Value.profileInfo with
                | None | Some None | Some (Some { displayName = "" }) | Some (Some { displayName = null }) ->
                    common.accountData.Value.selectedAccount
                | Some (Some { displayName = displayName }) -> displayName
            let msg =
                match transType with
                | Save -> "Saving your tree for the account:"
                | PostLike -> "Saving your reaction for the account:"
            div [ClassName "page-modal active"]
                [div [ClassName "saving-modal"]
                    [h1 [] [str msg]
                     div [ClassName "profile-info"] 
                        [match common.accountData.Value.profileInfo with
                         | None | Some None | Some (Some { image = "" }) | Some (Some { image = null }) ->
                            let seed = System.Int32.Parse(common.accountData.Value.selectedAccount.Substring(2, 8), NumberStyles.HexNumber)
                            jazzicon [Diameter 48; Seed seed] []
                         | Some (Some { image = image }) ->
                            img [ClassName "profile-image"; Src image]
                         h1 [] [str accountName]]
                     div [ClassName "lds-dual-ring"] []]]
         | _ -> ofOption None
         footerElement]
