module Home.View

open Fable.Core
open Fable.Core.JsInterop
open Fable.React
open Fable.React.Props
open Types
open Common
open Global
open System.Globalization

let connectBtn dispatch = div [] [button [ClassName "button home-button"; OnClick <| fun _ -> dispatch Connect] [str "Connect Account"]]

let root model common dispatch =
    div [ClassName "home"]
        [img [Src "/img/tree.svg"]
         h1 [] [str "Web3Tree"]
         match common.accountData with
         | None ->
            p [] [str "Connect your account to administer your Web3Tree"]
            connectBtn dispatch
         | Some accountData ->
            p [] [str "Connected as "; b [] [str accountData.selectedAccount]]
            div [ClassName "home-buttons"]
                [button [OnClick (fun _ -> dispatch <| ChangePage (View accountData.selectedAccount)); ClassName "button home-button"] [span [ClassName "icon"] [i [ClassName "mdi mdi-tree-outline"] []]; span [] [str "View Tree"]]
                 button [OnClick (fun _ -> dispatch <| ChangePage (Edit accountData.selectedAccount)); ClassName "button home-button"] [span [ClassName "icon"] [i [ClassName "mdi mdi-cog-outline"] []]; span [] [str "Edit Tree"]]]
         ]
