module App.State

open Elmish
open Elmish.Navigation
open Elmish.UrlParser
open Fable.Core
open Fable.Core.JsInterop
open Browser
open Global
open Types
open Imports
open Common
open System

let pageParser: Parser<Page->Page,Page> =
    let nftDetails a i = NftDetails (a, i)
    oneOf [
        map Home (s "")
        map Edit (s "edit" </> str)
        map nftDetails (s "nft" </> str </> str)
        map View (str)
    ]

let urlUpdate gbl (result : Page option) model =
    let setClasses className =
        (document.getElementById "html").className <- className
        (document.getElementById "body").className <- className
    match result with
    | None ->
        console.error("Error parsing url")
        model, Navigation.modifyUrl (toUrl model.page)
    | Some Home ->
        // set class
        setClasses "page-home"
        { model with
            page = Home
                // need to load user data
             }, Cmd.none
    | Some (NftDetails (address, id)) ->
        match model with
        | { page = View _; tree = { nftDetails = Some nftDetails } } when nftDetails.nft.token_address = address && nftDetails.nft.token_id = id ->
            model , Cmd.none
        | _ -> failwith "not implemented"
    | Some ((View user) as page) | Some ((Edit user) as page) ->
        match model with
        | { page = currentPage; tree = { nftDetails = Some _ } } when currentPage = page ->
            (Browser.Dom.document.getElementById "body").classList.remove("rmodal-open")
            (Browser.Dom.document.getElementById "html").classList.remove("rmodal-open")
            { model with tree = { model.tree with nftDetails = None } }, Cmd.none
        | _ ->
            setClasses <|
                match page with
                | View _ -> "page-view"
                | Edit _ -> "page-edit"
                | _ -> ""
            if user.StartsWith("0x") && user.Length = 42 then
                // address
                { model with page = page; tree = { model.tree with address = Some user; tree = None; treeEdit = None; mode = match page with | View _ -> New.Types.View | Edit _ -> New.Types.Edit } },
                    Cmd.batch
                        [Cmd.OfAsync.perform Server.getEns user (NewMsg << New.Types.Msg.SetEns)
                         Cmd.OfAsync.perform Server.getNFTs user (NewMsg << New.Types.SetNfts)
                         Cmd.OfAsync.perform Server.getTreeData user (NewMsg << New.Types.SetTreeData)]
            else
                // ens
                { model with page = page; tree = { model.tree with ens = Some (user + ".eth"); tree = None; treeEdit = None; mode = match page with | View _ -> New.Types.View | Edit _ -> New.Types.Edit } },
                    Cmd.OfAsync.perform Server.getAddress user (NewMsg << New.Types.Msg.SetAddress)

let init gbl result =
    let (home, homeCmd) = Home.State.init()
    let (model, cmd) =
        urlUpdate gbl result
          { page = Home
            home = home
            tree =
                { nfts = None
                  profileNft = None
                  tree = None
                  treeEdit = None
                  address = None
                  ens = None
                  mode = New.Types.PageMode.View
                  isSaving = None
                  maxNFTindex = 0
                  displayUntil = Config.nftPageSize
                  nftDetails = None
                  videoPlaying = None
                  likeEmojiInput = [] }
            common =
                { accountData = None } }
    model, Cmd.batch [ cmd
                       Cmd.map HomeMsg homeCmd ]

let update gbl msg model =
    let updateAccountCmd web3 =
        Cmd.OfPromise.perform
            (fun _ -> promise {
                let! chainId = web3?eth?getChainId()
                let! accounts = web3?eth?getAccounts()

                return { selectedAccount = accounts?at $ 0; chainId = chainId; profileInfo = None }
            }) () SetAccountData
    match msg with
    | HomeMsg Home.Types.Msg.Connect | NewMsg New.Types.Msg.Connect ->
        model, Cmd.OfPromise.perform gbl.web3Modal.connect () SetProvider
    | HomeMsg (Home.Types.Msg.ChangePage page) -> model, Navigation.newUrl <| toUrl page
    | HomeMsg msg ->
        let (home, homeCmd) = Home.State.update msg model.home
        { model with home = home }, Cmd.map HomeMsg homeCmd
            // SAVE PROFILE TX
    | NewMsg (New.Types.Msg.MakeTransaction New.Types.Save) ->
        match model.tree.treeEdit, model.common.accountData with
        | Some tree, Some account ->
            let markdown = tree |> TreeEditData.toTree |> TreeData.toMarkdown
            { model with tree = { model.tree with isSaving = Some New.Types.Save } }, Cmd.OfPromise.either
                (fun _ -> promise {
                    // perform update profile transaction
                    let! result = gbl.web3treeContract?methods?updateProfile(markdown)?send(createObj ["from", !!account.selectedAccount])
                    return result
                }) () (fun receipt -> NewMsg (New.Types.Msg.TransactionSuccess(New.Types.Save, receipt)))
                    (fun e -> NewMsg (New.Types.Msg.TransactionError(New.Types.Save, e)))
            // POST LIKE TX
    | NewMsg (New.Types.Msg.MakeTransaction New.Types.PostLike) ->
        match model.tree.likeEmojiInput, model.common.accountData, model.tree.nftDetails with
        | [], _, _ | _, None, _ | _, _, None -> 
            failwith "error no like input or account set"
        | emojis, Some account, Some nft ->
            { model with tree = { model.tree with isSaving = Some New.Types.PostLike } }, Cmd.OfPromise.either
                (fun _ -> promise {
                    let id = if nft.nft.token_id.StartsWith("0x") then nft.nft.token_id else "0x" + nft.nft.token_id
                    let text = System.String.Join(" ", emojis |> List.rev |> List.toArray)
                    let! result = gbl.web3treeContract?methods?addLike(nft.nft.token_address, id, text)?send(createObj ["from", !!account.selectedAccount])
                    return result
                }) () (fun receipt -> NewMsg (New.Types.Msg.TransactionSuccess(New.Types.PostLike, receipt)))
                (fun e -> NewMsg (New.Types.Msg.TransactionError(New.Types.PostLike, e)))
    | NewMsg (New.Types.Msg.TransactionError (_, e)) ->
        Browser.Dom.window?exn <- e
        Browser.Dom.window.alert e.Message
        model, Cmd.none
    | NewMsg msg ->
        let (new_, newCmd) = New.State.update msg model.page model.common model.tree
        { model with tree = new_ }, Cmd.map NewMsg newCmd
    | SetProvider provider ->
        let web3 = new Web3(provider)
        gbl.web3 <- web3
        gbl.web3treeContract <- web3.NewContract(Common.web3treeAbi, Common.Config.network.web3treeContractAddress)
        model, updateAccountCmd web3
    | SetAccountData accountData ->
        { model with common = { model.common with accountData = Some accountData } },
            Cmd.OfAsync.perform Server.getProfileInfo accountData.selectedAccount SetProfileInfo
    | SetProfileInfo profileInfo ->
        match model.common.accountData with
        | Some accountData ->
            { model with common = { model.common with accountData = Some { accountData with profileInfo = Some profileInfo } } }, Cmd.none
        | _ -> failwith "error no account set"
    | ChangePage page ->
        model, Navigation.newUrl <| toUrl page
    | RefreshAccounts ->
        model, updateAccountCmd gbl.web3
